import Swiper from 'swiper/swiper-bundle.js'
const Sliders = () => {
    const hasBlock = (selector) => {
      return document.querySelector(selector) == null
    }

    if(!hasBlock('.main-slider')){
        const topSlider = new Swiper(".main-slider",{
        slidesPerView:1,
        speed: 2000,
        spaceBetween: 14,
          pagination: {
          el: ".swiper-pagination",
          dynamicBullets: true,
          },
          navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        }
        })

    }


    if(!hasBlock('.home-product-box')){
      const newProd = new Swiper(".home-product-box",{
        slidesPerView:5,
        speed: 1000,
          spaceBetween: 20,
          navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        }
      })

    }
    if (!hasBlock('.array-touch')) {
      const touchbox = document.querySelectorAll('.array-touch')
      touchbox.forEach((item,index)=>{
        let w = item.offsetWidth
        let psevdo = item.querySelectorAll('.psevdo')
        psevdo.forEach((p,i)=>{
        let wd = w / psevdo.length
          p.style.cssText = `width:${wd}px;left:${wd * i}px`
          p.addEventListener('touchstart',ev => {
            changeImage(ev,p,item)
          })
          p.addEventListener('mousemove',ev => {
            changeImage(ev,p,item)
          })
        })
      })
    }
    function changeImage(ev,p,item){
      let image = ev.target.dataset.image
            let wrap = p.closest('.array-touch')
            let imageboxes = wrap.querySelectorAll('.array_image')
            let nmg = item.querySelector('.nan-img')
            let bullet = nmg.querySelectorAll('span')
            for(let i = 0; i<imageboxes.length;i++){
              imageboxes[i].classList.remove('hovered')
              bullet[i].classList.remove('hovered')
              }
            item.querySelector('#'+image).classList.add('hovered')
            item.querySelector('span[data-active='+image+']').classList.add('hovered')
    }
     if(!hasBlock('.sliders-thumbs')){
      const thumbsSlider = new Swiper(".sliders-thumbs",{
        slidesPerView:5,
        speed: 1000,
        spaceBetween: 14,
        direction:'vertical',
        preloadImages: true,
        lazy: true,
         navigation: {
          nextEl: ".pt-button-next",
          prevEl: ".pt-button-prev",
        }
      })

      const mainimage = new Swiper(".sliders-product-main",{
        slidesPerView:1,
        speed: 1000,
        spaceBetween: 14,
        preloadImages: false,
        effect: 'fade',
        fadeEffect: {
          crossFade: true
        },
        lazy: true,
        thumbs: {
          swiper: thumbsSlider
        }

      })
    }



}

export default Sliders

